@import "../../../css/module/fn";
@import "../../../css/module/color";
#result{
	position: absolute;
	
	width: p2m(600);
	height: p2m(600);
	top: p2m(200);
	left: 50%;
	margin-left: p2m(-300);
}
#round{
	width: p2m(500);
	height: p2m(500);
	border-radius: p2m(500);
	border: p2m(10) solid $red;
	margin: p2m(50) auto;
	
	
	font-size: p2m(90);
	color: $red;
	line-height: p2m(450);
	text-align: center;
}
#text{
	text-align: center;
	font-size: p2m(70);
	color:  $red;
	line-height: p2m(90);
}
#goDetail{
	position: fixed !important;
	bottom: 0;
}